// import { useState } from 'react';
import Select from 'react-select';
import css from './UserLocation.module.scss';
// import InputSearch from '../../../components/InputSearch/InputSearch';
import OrderData from '../../../components/OrderData/OrderData';
import YandexMap from '../../../components/YandexMap/YandexMap';

function UserLocation() {
    // const [searchCity, setSearchCity] = useState('');
    // const [searchPoint, setSearchPoint] = useState('');

    const options = [
        { value: '', label: '' },
        { value: '', label: '' },
        { value: '', label: '' },
        { value: '', label: '' },
    ];
    const pointOptions = [
        { value: ' 42', label: ' 42' },
        { value: '.', label: '.' },
        { value: '.', label: '.' },
    ];

    // const handleChangePoint = (e) => {
    //     setSearchPoint(e.target.value);
    // };

    return (
        <div className={css.contentBlock}>
            <div className={css.contentBlock__currentData}>
                <div className={css.formContainer}>
                    <label className={css.search} htmlFor="city">
                        <div className={css.search__label}></div>
                        {/* <div></div> */}
                        <Select
                            className={css.input}
                            classNamePrefix={css.input}
                            placeholder="   ..."
                            isClearable="true"
                            options={options}
                            id="city"
                            name="city"
                        />
                    </label>
                    <label className={css.search} htmlFor="point">
                        <div className={css.search__label}> </div>
                        {/* <div></div> */}
                        <Select
                            className={css.input}
                            classNamePrefix={css.input}
                            placeholder="   ..."
                            isClearable="true"
                            options={pointOptions}
                            id="point"
                            name="point"
                        />
                    </label>
                </div>
                <div className={css.description}>  :</div>
                <div className={css.map}>
                    <YandexMap />
                </div>
            </div>
            <div className={css.contentBlock__allOrderData}>
                <OrderData />
            </div>
        </div>
    );
}

export default UserLocation;
